import React from 'react';

export const Error = () => {
  return (
    <main id='Error' className='container'>
      <h1>Error: Path does not exist!</h1>
      <img className="w-50" src="./pages/images/spinning-turtle.gif" alt="Spinning Turtle" />
    </main>
  );
};

export default Error;



